<!-- eslint-disable max-len -->
<template>
  <section class="card-group row w-tab">
    <Loading :isLoading="isLoading" />
    <!-- 詳細商品 -->
    <ProductInformation :product="tempProduct" />
    <!-- 購物流程 -->
    <ShopProcess @addCart="addToCart" :carts="carts" />
  </section>
  <!-- 其他系列 -->
  <section class="card-group row w-tab text-center">
    <div class="card col-4">
      <div class="card-body ink-box">
        <button class="btn ink-box-double box-inner" @click="changeCate('pie')">法式甜點塔</button>
      </div>
    </div>
    <div class="card col-4">
      <div class="card-body">
        <button class="btn" @click="changeCate('cake')">乳酪蛋糕</button>
      </div>
    </div>
    <div class="card col-4">
      <div class="card-body">
        <button class="btn" @click="changeCate('cookie')">法式餅乾</button>
      </div>
    </div>
  </section>
  <!-- 相關產品 -->
  <section class="card-group row w-tab">
    <div class="card">
      <div class="card-body d-flex flex-wrap">
        <template v-for="item in products" :key="item.id">
          <div class="w25 mb-3" v-if="item.category === this.category">
            <a
              href="#"
              @click.prevent="changeProduct(item.id)"
              class="position-relative text-decoration-none"
            >
              {{ item.title }}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="-2 -4 150.93 80.58"
                class="circle-1 hex position-absolute top-50 start-50 translate-middle"
                v-if="item.id === this.tempProduct.id"
              >
                <path
                  fill="none"
                  stroke="#000"
                  stroke-miterlimit="10"
                  stroke-width="10px"
                  d="M51.36,8.75q-9.14-.64-18.3-.85-4.92-.16-9.85,0c-3.22.23-6.62.7-9.39,2.49A21.08,21.08,0,0,0,8,16.62,17.68,17.68,0,0,0,5.8,21.85c-.68,2.42-1.13,4.43-.48,6.94,1,3.79,3.48,7,6,9.91a60.49,60.49,0,0,0,15,12.44C33.6,55.43,41.8,57.88,49.91,60a144.68,144.68,0,0,0,27.72,4.25c10.65.62,21.78.65,32.27-1.6a48.14,48.14,0,0,0,10.86-3.49,16.81,16.81,0,0,0,6-4.33,11,11,0,0,0,1.91-9.37c-1.11-5.29-4.53-9.43-8.55-12.91a71.43,71.43,0,0,0-17.8-10.19Q89.91,17.05,77.08,13C66.09,9.53,54.58,6,43,5.14a32.64,32.64,0,0,0-8.67.34"
                ></path>
              </svg>
            </a>
          </div>
        </template>
      </div>
    </div>
  </section>
</template>

<style scoped>
.w25 {
  width: 25%;
}
.bordash {
  border-bottom: 2px dashed #000;
}
.circle-1 {
  width: 9rem;
  opacity: 0.9;
}
@keyframes dash {
  from {
    stroke-dashoffset: 1700;
  }
  to {
    stroke-dashoffset: 2000;
  }
}
.hex {
  stroke: #000;
  stroke-width: 2px;
  fill: none;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  animation: dash 0.35s linear;
  animation-fill-mode: forwards;
}
</style>

<script>
import emitter from '@/assets/javascript/emitter';
import ProductInformation from '@/components/front/ProductInformation.vue';
import ShopProcess from '@/components/front/ShopProcess.vue';
import Loading from '@/components/models/LoadingModel.vue';

export default {
  data() {
    return {
      isLoading: false,
      products: [],
      carts: [],
      category: 'pie',
      categoryList: [],
      tempProduct: {},
    };
  },
  components: {
    ShopProcess,
    ProductInformation,
    Loading,
  },
  watch: {
    // eslint-disable-next-line func-names
    'tempProduct.num': function () {
      if (this.tempProduct.num < 1 || typeof this.tempProduct.num !== 'number') {
        this.tempProduct.num = 1;
      }
    },
    carts() {
      emitter.emit('cartData', this.carts);
    },
  },
  methods: {
    getProducts() {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/all`;
      this.$http.get(api).then((res) => {
        this.products = res.data.products;
        this.setTempProducts();
      });
    },
    getCarts() {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http.get(api).then((res) => {
        this.carts = res.data.data;
      });
    },
    addToCart(data) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http
        .post(api, { data: { product_id: this.tempProduct.id, qty: 1, data } })
        .then((res) => {
          console.log(res);
          this.getCarts();
        });
    },
    setTempProducts() {
      this.products.forEach((item) => {
        if (this.category === item.category) {
          this.categoryList.push(item);
        }
      });
      // eslint-disable-next-line prefer-destructuring
      this.tempProduct = this.categoryList[0];
      this.isLoading = false;
    },
    changeProduct(id) {
      this.products.forEach((item) => {
        if (id === item.id) {
          this.tempProduct = item;
        }
      });
    },
    changeCate(category) {
      this.category = category;
      this.categoryList = [];
      this.setTempProducts();
    },
  },
  mounted() {
    if (this.$route.query.category) {
      this.category = this.$route.query.category;
    }
    this.getProducts();
    this.getCarts();
  },
};
</script>
