<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div class="card col-6">
    <div class="card-body">
      <section class="mb-3">
        <!-- size -->
        <Size />
        <!-- 提供餐具 -->
        <Tableware />
        <!-- 生日或是送禮 -->
        <Usage />
        <!-- 備註 -->
        <Note />
      </section>
      <section class="mb-4 pb-3">
        <h5>數量：</h5>
        <div class="d-flex">
          <nav aria-label="..." class="me-4">
            <ul class="pagination">
              <li class="page-item">
                <a class="page-link" href="#"><i class="bi bi-dash"></i></a>
              </li>
              <li class="page-item">
                <input type="text" class="page-link" style="width: 2.9em" />
              </li>
              <li class="page-item">
                <a class="page-link" href="#"><i class="bi bi-plus-lg"></i></a>
              </li>
            </ul>
          </nav>
          <div class="w-100">
            <button
              type="button"
              class="btn btn-outline-dark w-100"
              @click="$emit('addCart', this.orderOption)"
            >
              加入購物車
            </button>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import emitter from '@/assets/javascript/emitter';
import Size from '@/components/models/shop/SizeModel.vue';
import Tableware from '@/components/models/shop/TablewareModel.vue';
import Usage from '@/components/models/shop/UsageModel.vue';
import Note from '@/components/models/shop/NoteModel.vue';

export default {
  data() {
    return {
      orderOption: {
        size: '',
        tableware: '',
        usage: '',
      },
    };
  },
  props: ['carts'],
  components: {
    Size,
    Tableware,
    Usage,
    Note,
  },
  mounted() {
    emitter.on('size', (e) => {
      this.orderOption.size = e;
    });
    emitter.on('usage', (e) => {
      this.orderOption.usage = e;
    });
    emitter.on('tableware', (e) => {
      this.orderOption.tableware = e;
    });
  },
};
</script>
